@import (reference) 'variables.less'; // что бы webstorm не ругался на переменные

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: @font-main, sans-serif;
  font-weight: 400;
  color: @color-text-main;
}

ul {
  list-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}
