@import (reference) '../global/variables.less'; // что бы webstorm не ругался на переменные

.story {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
  max-width: 800px;
  width: 100%;
  padding: 24px 8px;
  gap: 8px;
}

.story__title {
  font-family: @font-second;
  font-size: 32px;
}

.story__text {
  font-size: 21px;
}
